@import "./Variables.scss";

.menu_title {
  font-size: $titleSize;
  font-weight: $mainWeight;
  margin-top: 10px;
  margin-bottom: 15px;
  color: $mainColor;
  margin-left: 10%;
}

.menu {
  padding: 0 5px;
  border-radius: 5px;
  width: 60%;
  margin: 0 auto;
  margin-bottom: 30px;

  h3 {
    padding: 5px 0 5px 25px;
    font-size: $resSize;
    font-weight: $mainWeight;
    color: $secondColor;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  button {
    box-shadow: 5px 7px #604444;
    background-color: $pageBackground;
    width: 98%;
    margin-bottom: 10px;
    border: solid 1px $pageBackground;
    border-radius: 4px;
    cursor: pointer;
    text-align: left;
    opacity: 0.9;
  }
  div {
    column-count: 2;
    .menu_text {
      padding: 5px;
      margin: 0 5px;
      display: flex;
      justify-content: space-between;
      color: $pageBackground;
    }
  }
}

@media screen and (max-width: 950px) {
  .menu {
    width: 80%;
  }
}

@media screen and (max-width: 700px) {
  .menu {
    width: 96%;
    div {
      column-count: 1;
    }
  }
}
