@import "./Variables.scss";

.homepage {
  text-align: center;

  h2 {
  font-size: $titleSize;
  font-weight: $mainWeight;
  margin-top: 10px;
  color: $mainColor;
  }
  .icons {
    .logo_links {
      display: inline-block;
      text-align: center;
      margin: 0 5px 5px 5px;
      h2 {
        font-size: $resSize;
      }

      img {
        width: 180px;
        border-radius: 5px;
        margin-bottom: 20px;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .homepage {
    h2{
        margin: 30px 5px 5px 5px;
    }
    .icons {
      .logo_links {
        img {
          width: 130px;
        }
      }
    }
  }
}

@media screen and (max-width: 560px) {
  .homepage {
    h2{
        margin: 10px 5px 5px 5px;
    }
    .icons {
      column-count: 2;
      .logo_links {
        img {
          width: 130px;
          margin-bottom: 5px;
        }
      }
    }
  }
}

